<template>
  <div class="board-column">
    <div class="board-column-header">
      {{ headerText }}
    </div>
    <el-scrollbar class="scroll-drawer">
      <draggable
        :list="list"
        v-bind="$attrs"
        class="board-column-content"
        draggable=".singlePerson"
        :set-data="setData"
        @end="onEnd"
      >
        <div v-for="element in list" :key="element.id" :class="isDraggable ? 'board-item singlePerson clearfix': 'board-item clearfix'">
          <span class="fl">{{ element.no }}</span><span class="fl"> {{ element.nickName }}</span>
          <el-button v-if="hasDelete" type="danger" icon="el-icon-delete" circle class="fr" @click="handleDel(element)" />
        </div>
      </draggable>
    </el-scrollbar>
  </div>
</template>

<script>
import draggable from 'vuedraggable'

export default {
  name: 'DragKanban',
  components: {
    draggable
  },
  props: {
    hasDelete: {
      type: Boolean,
      default: false
    },
    isDraggable: {
      type: Boolean,
      default: true
    },
    headerText: {
      type: String,
      default: 'Header'
    },
    options: {
      type: Object,
      default() {
        return {}
      }
    },
    list: {
      type: Array,
      default() {
        return []
      }
    }
  },
  methods: {
    handleDel(item) {
      this.$emit('deleteUser', item)
    },
    onEnd() {
      if (this.isDraggable) {
        this.$emit('handleDraggable')
      }
    },
    setData(dataTransfer) {
      // to avoid Firefox bug
      // Detail see : https://github.com/RubaXa/Sortable/issues/1012
      dataTransfer.setData('Text', '')
    }
  }
}
</script>

<style lang="scss" scoped>
.board-column {
  // 
  user-select: none;
  width: 220px;
  height: 400px;
  overflow: hidden;
  background: #f0f0f0;
  border-radius: 3px;
  .scroll-drawer{
    height: 330px;
  }

  .board-column-header {
    height: 50px;
    line-height: 50px;
    overflow: hidden;
    padding: 0 20px;
    text-align: center;
    background: #333;
    color: #fff;
    border-radius: 3px 3px 0 0;
  }

  .board-column-content {
    overflow: hidden;
    border: 10px solid transparent;
    min-height: 300px;
    // overflow-y: scroll;
    // display: flex;
    // justify-content: flex-start;
    // flex-direction: column;
    // align-items: center;

    .board-item {
      cursor: pointer;
      width: 100%;
      height: 39px;
      margin: 5px 0;
      background-color: #fff;
      text-align: left;
      line-height: 29px;
      padding: 5px 10px;
      box-sizing: border-box;
      box-shadow: 0px 1px 3px 0 rgba(0, 0, 0, 0.2);
      span{
        margin: 0 5px;
      }
    }
  }
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>

