<template>
  <el-dialog
    :title="`${title}`"
    :visible="value"
    top="6vh"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :show-close="false"
    :width="width"
    :lock-scroll="false"
    :before-close="beforeClose"
  >
    <!--  -->
    <div>
      <slot name="filter" />
    </div>
    <el-row>
      <el-table v-loading="loading" border :data="tableData" align="center" style="width: 100%" :highlight-current-row="true" :stripe="false" @row-click="handleClickParFome">
        <el-table-column v-for="item in formThead" :key="item.label" :label="item.label" :formatter="item.formatter" :prop="item.prop" align="center" />
      </el-table>
    </el-row>
    <pagination v-if="usePage" :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="pageStep" />
    <!--  -->
    <el-row>
      <slot name="extra" />
    </el-row>
    <div slot="footer">
      <el-button type="primary" @click="handleConfirm"> </el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import Pagination from '@/components/Pagination'

export default {
  name: 'MaterielCodeComs',
  components: {
    Pagination
  },
  mixins: [paginationPage],
  props: {
    width: {
      type: String,
      default: '840px'
    },
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    responseData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    formThead: {
      type: Array,
      default: () => {
        return []
      }
    },
    loading: {
      type: Boolean,
      default: false
    },
    extraHandle: {
      type: Object,
      default: () => {
        // Fn handleRowClick: 
        return {}
      }
    },
    isChoose: {
      type: Boolean,
      default: true
    },
    usePage: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      tableData: [],
      chooseItem: {}
    }
  },
  watch: {
    responseData: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  methods: {
    handleConfirm() {
      console.log(this.chooseItem, 'chooseItem')
      if (!this.isChoose) {
        this.$emit('input', false)
        return
      }

      // if (Object.keys(this.chooseItem).length <= 0) {
      //   this.$message({
      //     message: `${this.title}`,
      //     type: 'error'
      //   })
      //   return
      // }
      this.$emit('chooseColumn', this.chooseItem)
      this.$emit('input', false)
    },
    init() {
      console.log(this.formThead, 'asdf')
      if (!this.responseData.list) return
      this.chooseItem = {}
      const { list, ...pageInfo } = this.responseData
      this.initPage(pageInfo)
      this.tableData = list
    },
    pageStep() {
      this.$emit('pageStep', this.pageInfo)
    },
    beforeClose() {
      this.chooseItem = {}
      this.tableColumnChooseQuery = {}
      this.$emit('input', false)
      this.$emit('clear')
    },
    handleClickParFome(row, column, cell, event) {
      if (column.label === '') {
        return false
      }
      this.chooseItem = { ...row }
      if (this.extraHandle.handleRowClick) {
        console.log('emit')
        this.$emit(this.extraHandle.handleRowClick, this.chooseItem, (valid) => {
          console.log('===', valid)
          if (!valid || valid !== 'stop') {
            this.$emit('input', false)
          }
        })
      }
    }
  }
}
</script>
<style lang="scss" scoped>
</style>
