<template>
  <el-dialog
    :title="title"
    top="10vh"
    :visible="value"
    :show-close="false"
    width="800px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :before-close="beforeClose"
    @open="initDeps"
  >
    <slot name="extraBefore" />
    <el-row :gutter="10">
      <!--  -->
      <el-col :span="8" class="top-filter">
        <el-input v-model="filterText" clearable prefix-icon="el-icon-search" placeholder="" @input="initDeps" />
        <el-tree v-loading="deptsTreeLoadingMixins" :data="deptsMixins" :props="defaultPropsMixins" :expand-on-click-node="false" node-key="id" :default-expanded-keys="depsDefaultExpandKeysMixins" @node-click="handleNodeClick" />
      </el-col>
      <!--  -->
      <el-col :span="8">
        <Kanban :key="1" :list="list1" :group="group" :is-draggable="isDraggable" :has-delete="false" class="kanban todo" header-text="" @handleDraggable="handleDraggable" />
      </el-col>
      <el-col :span="8">
        <Kanban :key="2" :list="list2" :group="group" :is-draggable="false" :has-delete="true" class="kanban done" header-text="" @deleteUser="deleteUser" />
      </el-col>
    </el-row>
    <slot name="extraAfter" />
    <div slot="footer">
      <el-button :loading="loading" type="primary" @click="dialogComfirm()"> </el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import Kanban from './kanban'
import { getUserInGroupApi } from '@/apis/modules/common'
import { deptsOptionsNoCatch } from '@/mixins/deptsOptions'

export default {
  name: 'OriginUserTreeComs',
  components: {
    Kanban
  },
  mixins: [deptsOptionsNoCatch],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    // 
    getUserUrl: {
      required: true,
      type: String
    },
    // id
    id: {
      type: [Number, String],
      default: ''
    },
    // 
    axiosModule: {
      type: String,
      default: 'wms'
    },
    urlModule: {
      type: String,
      default: ''
    },
    idName: {
      type: String,
      default: 'id'
    },
    // id  {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // 
    multipleDetail: {
      type: Boolean,
      default: false
    },
    // 
    max: {
      type: Number,
      default: 0
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      depsLoading: false,
      loading: false,
      filterText: '',
      query: {
        companyId: '',
        officeId: ''
      },
      tableData: [],
      group: 'mission',
      list1: [],
      list2: [],
      isDraggable: true
    }
  },
  methods: {
    // 
    initDeps() {
      this.initDepsMixins({ name: this.filterText, ...this.extraInfo })
    },
    init() {
      // id
      if (!this.id && this.axiosModule !== 'sys') {
        return
      }
      return new Promise((resolve, reject) => {
        this.loading = true
        const url = this.getUserUrl
        /**
         * {
         *  cutData: {...this.query}
         * }
         * 
         */
        const params = {
          ...this.query
        }
        //     
        if (this.axiosModule !== 'sys') {
          params[this.idName] = this.id !== 'null' ? this.id : ''
        }
        getUserInGroupApi(url, params, this.axiosModule).then(res => {
          const { code, data } = res
          if (!code) {
            const userList = data
            const filterList = this.filterUserByRight(userList, this.list2)
            this.list1 = filterList
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    deleteUser(item) {
      const { id } = item
      this.list2 = this.list2.filter(item => item.id !== id)
      this.init()
      // 
      this.handleDraggable()
    },
    filterUserByRight(source, target) {
      if (target.length <= 0) return source
      if (source.length <= 0) return []
      const users = source
      for (let i = 0; i < source.length; i++) {
        for (let j = 0; j < target.length; j++) {
          if (source[i].id === target[j].id) {
            users.splice(i, 1)
          }
        }
      }
      return users
    },
    dialogComfirm() {
      if (this.list2.length <= 0) {
        this.$notify({
          message: '',
          type: 'error'
        })
        return
      }
      if (this.multiple) {
        if (this.multipleDetail) {
          this.$emit('submitUserList', this.list2)
        } else {
          const userIds = this.list2.map(item => item.id)
          this.$emit('submitUserList', userIds)
        }
      } else {
        this.$emit('submitUserList', this.list2[0])
      }
      this.beforeClose()
    },
    // getDeptDatas(node, resolve) {
    //   const params = {}
    //   if (typeof node !== 'object') {
    //     if (node) {
    //       params['name'] = node
    //     }
    //   } else if (node.parentId !== 0) {
    //     params['id'] = node.data.id
    //   }
    //   setTimeout(() => {
    //     getListByPidApi(params).then(res => {
    //       const { code, data } = res
    //       if (!code) {
    //         if (resolve) {
    //           resolve(data)
    //         } else {
    //           this.depts = data
    //         }
    //       }
    //     })
    //   })
    // },
    handleNodeClick(data) {
      const { type, id } = data
      if (type === '1') {
        // 
        this.query.companyId = id
        this.query.officeId = null
      } else if (type === '2') {
        // 
        this.query.officeId = id
        this.query.companyId = null
      } else {
        // type
        this.list1 = []
        return
      }
      this.init()
    },
    filterNode(value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    handelCheckChange(data, checked, indeterminate) {
      console.log(data, checked, indeterminate)
    },
    // 
    handleDraggable() {
      if (!this.multiple && this.list2.length > 0) {
        this.isDraggable = false
      } else if (!this.multiple && this.list2.length === 0) {
        this.isDraggable = true
      }
      // 
      if (this.multiple && this.max && this.list2.length >= this.max) {
        this.$notify({
          title: '',
          message: ` ${this.max} `,
          type: 'warning'
        })
        this.isDraggable = false
      } else if (this.multiple && this.max && this.list2.length < this.max) {
        this.isDraggable = true
      }
    },
    clearData() {
      this.list1 = []
      this.list2 = []
    },
    beforeClose(done) {
      this.list1 = []
      this.list2 = []
      this.isDraggable = true
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang='scss'>
.kanban {
  &.todo {
    .board-column-header {
      background: #4A9FF9;
    }
  }
  &.working {
    .board-column-header {
      background: #f9944a;
    }
  }
  &.done {
    .board-column-header {
      background: #2ac06d;
    }
  }
}
</style>
