import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import { LOAD_CHILDREN_OPTIONS } from '@riophae/vue-treeselect'
import { getDeptApi, getListByPidApi, getDepsNoCacheApi } from '@/apis/modules/system/dept'

export const deptsOptions = {
  components: {
    Treeselect
  },
  data() {
    return {
      defaultProps: { children: 'children', label: 'name', hasleaf: true },
      depts: []
    }
  },
  methods: {
    initOrigin(uploadAll, deptName) {
      return new Promise((resolve, reject) => {
        getDeptApi({}).then(res => {
          const { code, data } = res
          if (!code) {
            if (uploadAll) {
              // 
              this.depts = this.mapDeptsTree(data)
            } else {
              // 
              this.depts = data.map(item => {
                return { ...item, label: item.name, children: null }
              })
              // console.log(this.depts)
            }
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    loadDepts({ action, parentNode, callback }) {
      if (action === LOAD_CHILDREN_OPTIONS) {
        const params = parentNode.id ? { id: parentNode.id } : {}
        getListByPidApi(params).then(res => {
          if (!res.code) {
            let { data } = res
            data = JSON.parse(JSON.stringify(data).replace(/name/g, 'label').replace(/children/g, 'children'))
            parentNode.children = data.map((obj) => {
              if (obj.hasleaf) {
                obj.children = null
              } else {
                delete obj.children
              }
              return obj
            })
          }
          setTimeout(() => {
            callback()
          }, 100)
        })
      }
    },
    mapDeptsTree(tree) {
      if (!Array.isArray(tree)) return
      const data = tree.map(item => {
        const { name: label, children, ...info } = item
        if (!item.hasleaf) {
          return { ...info, label }
        } else {
          const child = this.mapDeptsTree(children)
          return { ...info, label, children: child }
        }
      })
      return data
    }
  }
}

/*
*    
*/
export const deptsOptionsNoCatch = {
  data() {
    return {
      deptsTreeLoadingMixins: false,
      defaultPropsMixins: {
        children: 'children',
        label: 'name',
        isLeaf: (data, node) => {
          return !node.data.hasleaf
        }
      },
      deptsMixins: [],
      depsDefaultExpandKeysMixins: [],
      depsFilterText: ''
    }
  },
  methods: {
    // 
    initDepsMixins(queryObj) {
      const params = queryObj || { name: this.depsFilterText }
      return new Promise((resolve, reject) => {
        this.deptsTreeLoadingMixins = true
        getDepsNoCacheApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            this.depsDefaultExpandKeysMixins = data.map(item => item.id)
            this.deptsMixins = data
          }
          this.deptsTreeLoadingMixins = false
          resolve(res)
        }).catch(err => {
          this.deptsTreeLoadingMixins = false
          reject(err)
        })
      })
    }
  }
}
